.photoFrame {
  margin-right: 2rem;
  width: 40vw;
  height: auto;
  aspect-ratio: 1.35/1 auto;
  overflow: hidden;
}
.photoFrameImg {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit:fill;
}

.photoshootlayoutmaintextcontainer {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  width: auto;
  height: auto;
}

.photoshootlayoutbox {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 1rem;
  aspect-ratio: 1/1;
  width: 30%;
  margin: 1rem;
  margin-top: 3rem;
}

