.background {
  width: 30rem;
  height: 13rem;
  background-color: white;
  border-radius: 10px;
  margin: 1rem;
}

.buttonBlack {
  font-size: 2.5rem;
  margin: 1px;
  width: fit-content;
  float: right;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  transition: color 0.3s ease;
  padding-left: 1rem;
}

.buttonBlack:hover {
  color: #f400b0;
}

.wrongMassage {
  color: red;
  /* padding: 10px; */
  font-size: 3rem;
  animation: fadeout 2s;
  -moz-animation: fadeout 5s; /* Firefox */
  -webkit-animation: fadeout 2s; /* Safari and Chrome */
  -o-animation: fadeout 5s; /* Opera */
  animation-fill-mode: forwards;
  z-index: 1;
  position: absolute;
  bottom: 17%;
  left: 51%;
  display: none;
}

.textWithIcon {
  font-size: 1.2rem;
}
.buttonColor {
  color: white;
  font-size: 2rem;
  display: inline-block;
  text-align: center;
  height: 3rem;
  width: 4rem;
  /* border: solid rgba(129, 101, 101, 0.151); */
  margin: 1px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  transition: scale .4s ease;
}

.buttonColor:hover{
    scale: 1.15;
}
.result {
  display: flex;
  align-items: center;
  font-size: 3rem;
}

.Container {
  font-size: 2.5rem;
  display: flex;
  flex-direction: row;
  gap: 2rem;
}

.AnswerFont {
  word-break: keep-all;
  text-overflow: ellipsis;
}

.Background {
  display: flex;
  flex-direction: column;
}

.palette {
  padding-top: 1rem;
  height: 2rem;
}

.canvas {
  border-radius: 1rem;
}
