.card {
  max-width: 100%;
  display: inline-block;
  border: none;
  margin: 2rem 5rem 4rem 2rem;
}

.HostInfo {
  display: inline-flex;
  align-items: center;
  gap: 1rem;
}

.HostImage {
  width: 5rem;
  border-radius: 50%;
  aspect-ratio: 1/1;
  border: solid 0.4rem transparent;
  background-image: linear-gradient(#fff, #fff), 
  linear-gradient(to right, #8f00ff 0%,  #f400b0 100%);
  background-origin: border-box;
  background-clip: content-box, border-box;
}

.HostFollowingsList {
  margin: auto;
  width: 100%;
  display: inline-flex;
  overflow: scroll;
}

.HostFollowingsList::-webkit-scrollbar {
  height: .8rem;
}

.HostFollowingsList::-webkit-scrollbar-thumb  {
  background-color: #f400b0;
  border-radius: 1rem;
}

.HostFollowingsList::-webkit-scrollbar-track  {
  background-color:#222222;
}

.HostFollowingsList::-webkit-scrollbar-corner{
  display: none;
}

.HostTextInfo {
  justify-content: center;
  align-items: center;
  margin: auto;
  line-height: 3rem;
  overflow: hidden;
  white-space: normal;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  
}

.LevelFont {
  display: inline-block;
  font-size: 1.5rem;
}

.userNameFont {
  font-size: 2rem;
}

.RoomInfo {
  text-align: start;
    font-size: 2rem;
    width: 100%;
    margin: 0;
    padding-left: 1.2rem;
    white-space: normal;
    cursor: pointer;
    text-overflow: ellipsis;
    overflow: hidden;
    line-height: 1.2;
    min-height: 2.4em;
    height: fit-content;
    word-wrap: break-word;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.Roomimage {
  width: 20rem;
  align-items: center;
  border-radius: 2.5rem;
  cursor: pointer;
}

